import React, { Component } from 'react'
import sapLogo from '../Assets/sapLogo.svg'
import { Grid } from '@material-ui/core'
import './Header.css'
// import Button from '@material-ui/core/Button';
// import IconButton from '@material-ui/core/IconButton';
// import HomeIcon from '@material-ui/icons/Homeimp[ort]


class Header extends Component {

    btnClick() {
        window.open("https://www.sap.com/india/about/labs-india.html");
    }

    render(){
        return (
            <div className="MainDiv">   
            <Grid container spacing={6}>
            <Grid item xs={5}>
                <img src = {sapLogo} className = "sapLogo"/></Grid>
                <button className="Home" variant="outlined" onClick={this.btnClick.bind(this)}>HOME</button>
                <button className="Predict" variant="outlined">PREDICT</button>
            </Grid>
        </div>
        )
        
    }
}

export default Header